@mixin center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay {
  @include center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .5);
  z-index: 9999;
}

.wrapper {
  //width: 300px;
  //height: 300px;
  border-radius: 20px;
  box-shadow: 0 2px 30px 0 rgba(0, 0, 0, .4);
  background-color: #fff;
}
