@import '~antd/dist/antd.css';

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
    cursor: pointer;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.center {
    display: grid;
    place-items: center;
}

/*-------------------------------------------------*/

.nav {
    display: flex;
    align-items: center;
    width: 100vw;
    height: 50px;
    background-color: #20232a;
}

.nav-item {
    display: grid;
    place-items: center;
    width: 62px;
    height: 50px;
    font-size: 16px;
    font-weight: 300;
    color: #fff;
}

.nav-item-select {
    position: relative;
    color: #61dafb;
}

.nav-item-select::before {
    content: '';
    position: absolute;
    top: 46px;
    left: 0;
    width: 62px;
    height: 4px;
    background-color: #61dafb;
}

.nav-link {
    margin: 0 30px 0 auto;
}

.nav-link-item {
    padding: 5px 10px;
    color: #fff;
}

.list-item {
    font-size: 12px;
}

/*-------------------------------------------------*/
.language-box {
    text-align: center;
}

.img-margin {
    margin: 30px auto;
}

/*-----------------antd design----------------------*/
.row-margin {
    margin: 8px 0;
}

.col-bg-one {
    height: 54px;
    background-color: #40adff;
}

.col-bg-two {
    height: 54px;
    background-color: #0092ff;
}

/*-----------------setting box----------------------*/
.setting-box{
    overflow: auto;
    width: 100vw;
    height: 100vh;
    transition: all .3s;
}
.setting-item {
    margin: 10px 15px;
    padding: 5px 5px 5px 35px;
    width: calc(100vw - 30px);
    box-sizing: border-box;
    border-radius: 22px;
}
.switch-box{
    position: fixed;
    top: 60vh;
    right: 3vw;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: #1890ff;
}

.main-title {
    font-size: 21px;
}

.subtitle {
    font-size: 12px;
}

.custom-btn{
    margin: 8px 0;
}