.verificationCodeInput {
  margin-right: 8px;
  //padding: 10px 20px 11px;
  padding: 10px 0 11px 0;
  text-align: center;
  width: 54px;
  height: 54px;
  border-radius: 4px;
  border: solid 1px rgba(0, 0, 0, 0.12);
  font-size: 24px;
  outline: none;
  transition: all .1s;

  &:focus {
    box-shadow: 0 0 6px 0 rgba(158, 158, 158, 0.5);
  }

  &:last-child {
    margin-right: 0;
  }

}
