.outer-box {
  width: 200px;
  height: 200px;
  background-color: #40adff;

  .inner-box {
    width: 100px;
    height: 100px;
    background-color: #c67a88;
  }

}

.use-memo-box {
  margin: 0 auto;
  width: 300px;
  border: 1px solid #40adff;
}

.ref-box {
  display: grid;
  place-items: center;
  width: 200px;
  height: 200px;
  border: 1px solid #40adff;
  font-size: 24px;
  font-weight: 600;
}

.to-do-btn {
  padding: 7px 5px;
  width: fit-content;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #a9a9a9;
  }

}
